@import "colors.scss";
@import "fonts.scss";

html {
    box-sizing: border-box;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
a {
    text-decoration: none;
}
*,
*:before,
*:after {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

.page {
    position: relative;
    font-family: $SansPro;
}
.top-block {
    background-image: url(/images/Rectangle5.2.png);
    background-size: cover;
    height: 540px;
    &__content {
        color: #fff;
    }
    &__header {
        font-weight: 700;
        font-size: 42px;
    }
    &__info {
        font-weight: 400;
        font-size: 16px;
    }
    &__other {
        font-weight: 600;
        font-size: 18px;
        margin-bottom: 70px;
    }
    .link {
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 207px;
        height: 60px;
        background-color: #8abf42;
        font-weight: 400;
        font-size: 18px;
    }
}
.menu {
    position: fixed;
    width: 8.33vw;
    height: 100%;
    background-color: #e0e0e0;
    z-index: 1;
    margin-left: -15px;
}
.header {
    height: 100px;
    display: flex;
    &__row {
        height: 100%;
    }
    &__logo{
        display: flex;
    }
    &__phone {
        margin-right: 2.625em;
        font-weight: 700;
        font-size: 1.125em;
    }
    &__numbers {
        color: #fff;
    }
    &__phone-second {
        margin-right: 0.6875em;
        font-weight: 700;
        font-size: 1.125em;
    }
    &__info {
        font-weight: 400;
        font-size: 0.875em;
    }
    &__geo{
        display: flex;
    }
    &__city {
        color: #fff;
        font-weight: 600;
        font-size: 1.125em;
        margin-left: 3px;
    }
    &__search{
        display: flex;
    }
    
    
}

.main {
    position: relative;

    &__content {
       
        &-start {
            font-weight: 700;
            font-size: 26px;
        }
        &-text {
            font-weight: 300;
            font-size: 1.5em;
        }

        &-end {
            font-weight: 300;
            font-size: 1.125em;
        }
    }
}
